import React, {Component} from 'react';
import PropTypes from 'prop-types';
import AlloyTouch from './AlloyTouch';
import Transform from 'alloytouch/transformjs/transform';

//  https://github.com/AlloyTeam/AlloyTouch 
class ReactAlloyTouch extends Component {
  //
  static propTypes = {
    children: PropTypes.node, // 
    className: PropTypes.string, //  className
    prefix: PropTypes.string, // 
    options: PropTypes.object, // AlloyTouch 
    transform: PropTypes.bool, //  Transform 
    extensionFunc: PropTypes.func, // 
    updateFunc: PropTypes.func, //  componentDidUpdate 
  };

  static defaultProps = {
    className: '',
    prefix: 'alloy',
    options: {},
    transform: true,
  };

  componentDidMount() {
    const {wrapper, scroller} = this.refs;
    const {options, transform, extensionFunc} = this.props;

    //  options  wrapper  scroller
    Object.keys(options).forEach((func) => {
      if (typeof func === 'function') {
        options[func] = function (...args) {
          options[func](...args, wrapper, scroller);
        }
      }
    });

    if (transform) {
      Transform(scroller, true);
    }

    const min = wrapper.clientHeight - scroller.scrollHeight;

    const alloyOptions = {
      touch: wrapper, // dom
      target: scroller, // 
      vertical: true, // truetouch
      property: 'translateY',  // 
      sensitivity: 1, // ,1
      factor: 1, // ,1
      min,
      max: 0, // ,
      step: 40, // step
      ...(options || {})
    };

    //  alloyTouch 
    this.alloyTouch = new AlloyTouch(alloyOptions);

    if (typeof extensionFunc === 'function') {
      extensionFunc(this.alloyTouch, wrapper, scroller);
    }
  }

  componentDidUpdate() {
    const {updateFunc} = this.props;
    const {wrapper, scroller} = this.refs;
    if (typeof updateFunc === 'function') {
      updateFunc(this.alloyTouch, wrapper, scroller);
    }
  }

  render() {
    const {
      children, className, prefix
    } = this.props;

    return (
      <div className={`${prefix}-wrapper ${className || ''}`} ref="wrapper">
        <div className={`${prefix}-scroller`} ref="scroller">
          {children}
        </div>
      </div>
    );
  }
}

export default ReactAlloyTouch;



